.grids {
  width: 50%;
  margin: auto;
  height: 380px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  background-color: white;
  /* border:1px solid black; */
  position: absolute;
  right: 90px;
  top: -20px;
  padding: 10px 0px;
  border-radius: 5px;
  box-shadow: 0px 4px 10px rgba(0, 76, 255, 0.212);
}

.gridContainer {
  display: flex;
  background-color: #fef9f4;
  width: 60%;
  margin: 60px auto;
  padding: 30px;
  height: 300px;
  position: relative;
  border-radius: 16px 6px;
}

