.footer {
  width: 100%;
  /* background-color: #4fadff; */
  /* position: fixed; */
  /* bottom:0; */
  padding-bottom: 20px;
  box-shadow:0px 4px 10px rgba(0, 76, 255, 0.507);

  /* border:1px solid #c2c1c1; */
}

@media(max-width: 400px){
   .footerContainer{
    width: 100%;
   }
}
.footerContainer {
  display: flex;
  width: 40%;
  margin: auto;
  justify-content: space-between;
  align-items: center;
  padding: 0px;
  /* color: white; */
  opacity: 0.8;
}

.left {
  margin-left: 100px;
}

.name {
  display: flex;
  align-items: center;
}
.btn {
  font-size: 20px;
  /* background-color: rgb(241, 241, 245); */
  border: none;
  color: #4fadff;
  padding: 10px;
  width: 200px;
}

.icons {
  -webkit-box-shadow: -4px 0px 0px -1px rgba(92, 87, 92, 1);
  -moz-box-shadow: -4px 0px 0px -1px rgba(92, 87, 92, 1);
  box-shadow: -4px 0px 0px -1px rgba(92, 87, 92, 1);
  padding: 20px;
  /* display: flex; */
  /* flex-direction: column; */
  margin-right: 100px;
}

.socialIcons {
  padding: 5px;
  font-size: 30px;
  /* color: white */
}
